:root {
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-light: rgba(255, 255, 255, 0.6);
    --color-white: #fff;
    --color-red: rgb(240, 51, 51);

    /*screen size */
    --container-width-lg: 75%;
    /* desktop */
    --container-width-md: 86%;
    /* tablet */
    --container-width-sm: 90%;
    /*mobile */

    --transition: all 400ms ease;
}

header {
    padding-top: 3rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.profile_photo {
    margin: 2rem auto;
    width: 300px;
    border-radius: 5rem;
    box-shadow: 1px 2px 10px #000;
}

/*
====================
CTA 
====================
*/

.cta {
    margin-top: 1.5rem;
    display: flex;
    justify-content: center;
    gap: 1.2rem;
}

/*
====================
social
====================
*/

.header__socials {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.8rem;
    margin: 5px 0;
}

/*
====================
Scroll down 
====================
*/

.scroll__down {
    font-size: 2rem;
    font-weight: 700;
}